.mas-article{
    background-size: cover;
    position: relative;
}
.mas-article::after{
    content: "";
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    background-color: rgba(255,255,255,0.7);
}
.mas-text{
    padding: 20px;
    text-align: center;
    position: relative;
    z-index: 2;
}
  .Modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background-color: white;
    overflow: auto;
  }

  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.7);
    z-index: 3;
  }
@media only screen and (max-width: 700px) {

    .Overlay{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.7);
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Modal{
        position: absolute;
        left: 0;
        top: auto;
        bottom: auto;
        right: 0;
        z-index: 3;
        background-color: white;
    }
    .mas-container {
        grid-column: 3/11;
    }
}