body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
    background-color: white;
    position: fixed;
    width: 100%;
    height: 7vh;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    box-shadow: -3px 3px 6px 1px rgba(0,0,0,0.5);

}
.header h1{
    margin: 0;
}
.header-name{
    color: #E01171;
}

@media only screen and (min-width: 700px) {
    
    .header {
    }
}

@font-face {
    font-family: 'Montserrat';
    src: url(/marca-personal/static/media/Montserrat-Regular.27e50ffd.otf);
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'Montserrat';
    src: url(/marca-personal/static/media/Montserrat-Black.6d1796a9.ttf);
    font-weight: 900;
    font-style: normal;
}
/* @font-face {
    font-family: 'Montserrat';
    src: url('../../fonts/Montserrat-ExtraBold.otf');
    font-weight: 800;
    font-style: normal;
} */
a{
  text-decoration: none;
}
h1{
  color: white;
  font-weight: 300;
  font-size: 1.5rem;
  margin: 20px;
}
p{
  color: #4A4A4A;
  font-size: .8rem;
  margin: 0;
}
body {
  background-image: url(/marca-personal/static/media/Back.e58c8fb2.svg);
  background-color: #0F0766;
  background-size: cover;
  margin: auto;
  font-family: 'Montserrat';
}
h2{
  color: white;
  font-weight: 800;
  font-size: 1.5rem;
  margin: 20px;
  text-align: center;
}
h3{
  color: #0F0766;
  font-weight: 300;
  font-size: 1.2rem;
  margin: 20px 0 5px 20px;
}



.desktop-layout{
    width: 700px;
    margin: 10vh auto;

}
@media only screen and (min-width: 800px){
    .desktop-layout{
        width: 800px;
        margin: 10vh auto;
    
    }
}
.resumen-container{
    margin: 20px;
    display: flex;
    flex-direction: column;
    box-shadow: -3px 3px 6px 1px rgba(0,0,0,0.5);

}
.resumen-image{
    width: 100%;
}
.name{
    margin: 0;
    color: #59057B;
}
.article{
    background-color: white;
    padding: 10px;
}
.horizontal-line{
    background-color: #0F0766;
    height: .1rem;
    margin-top: 7px;
    margin-bottom: 10px;
}
.sobremi{
    margin-bottom: 10px;
}
.resumen-item{
    display: flex;
    align-items: center;
    margin-top: 10px;
    /* justify-content: space-between;  */
}
.item-highlight{
    background: #E01171;
    padding: 3px 10px 5px 10px;
    color: white;
}
.item-value{
    margin-left: 10px;
}

@media only screen and (min-width: 700px) {
    
    .resumen-container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr .8fr;
        margin: 0;
    }
    .resumen-image{
        grid-column: 1/2;
        grid-row: 1/3;
    }
    .article-resumen{
        display: flex;
        flex-direction: column;
        justify-content: center;
        grid-column: 2/3;
        grid-row: 1/2;
        padding: 0 2vw 0 2vw;
    }
    .horizontal-line{
        height: .2rem;
        background-color: #E01171;
    }
    .redes-desktop-container{
        grid-row: 2/3;
        grid-column: 2/3;
        background-color: white;
        padding: 0 2vw 0 2vw;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }
}

@media only screen and (max-width: 700px) {
    
    .resumen-margin{
        margin-top: 10vh;
    }
}
.estudios-article{
    background-color: white;
    padding: 0 0 20px 0;
    margin: auto auto 20px auto;
    width: 90%;
    box-shadow: -3px 3px 6px 1px rgba(0,0,0,0.5);

}
.horizontal-line-pink{
    background-color: #E01171;
    height: .2rem;
    margin-bottom: 10px;
}
.article-p{
    margin-left: 20px;
}
@media only screen and (min-width: 700px) {
    
    .estudios-desktop {
        grid-column: 3/11;
    }
}
.title-skills{
    text-align: center;
    margin-left: 0;
    margin-top: 20px;
    margin-right: 0;
    
}
.small-article{
    background-color: white;
    padding: 0 0 20px 0;
    margin: auto auto 20px auto;
    box-shadow: -3px 3px 6px 1px rgba(0,0,0,0.5);

}
.skills-image{
    width: 25vw;
}

.images-container{
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 5px;
    margin-left: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.skill-container{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.skills-image-firebase{
    width:13vw;
}

@media only screen and (min-width: 700px) {
    
    .skills-desktop {
        display: grid;
        grid-column-gap: 20px;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 10vh 1fr;
    }
    .skills-article{
        margin: 0;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .skills-title{
        grid-column: 1/3;
        grid-row: 1/2;
    }

    .skills-image-desktop{
        height: 72px;
        width: auto;
    }
    .images-container{
        margin: 20px auto;
        width: 65%;
    }
}
.portfolio-container{
    margin-left: 20px;
    margin-right: 20px;
}
.small-article-portfolio{
    width: 45%;
    margin: 0 0 20px 0;
    padding: 0;
}
.portfolio-flex{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.images-container-portfolio{
    padding: 0;
    margin: 0;
    height: 30vh;
}
.portfolio-item-container{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.portfolio-text{
    margin-top: 10px;
    margin-bottom: 10px;

}

@media only screen and (min-width: 700px) {
    
    .portfolio-image{
        height: 120px;
    }
    .images-container-portfolio{
        width: 100%;
    }
}
.redes-container{
    flex-direction: column;
}
.redes-article{
    padding: 0;
    box-shadow: none;
}
.redes-text{
    margin: 20px 0 20px 0;
}
@media only screen and (min-width: 700px) {
    .redes-images-container{
        display: flex;
    }
    .red-container{
        margin-bottom: 0;
    }
    .redes-desktop {
        grid-column: 3/11;
    }
    .redes-container{
        flex-direction: row;
        flex-wrap: nowrap;
        margin: 0;
        justify-content: space-between;
    }
    .redes-article{
        margin: 0;
    }
    .redes-image{
        width: 32px;
    }
    .redes-line{
        margin-bottom: 30px;
        margin-left: auto;
        margin-right: auto;
    }
    .redes-title{
        color: #59057B;
        text-transform: capitalize;
        margin-left: 0;
        text-align: left;
        font-weight: 300;
    }
}
.mas-article{
    background-size: cover;
    position: relative;
}
.mas-article::after{
    content: "";
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    background-color: rgba(255,255,255,0.7);
}
.mas-text{
    padding: 20px;
    text-align: center;
    position: relative;
    z-index: 2;
}
  .Modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background-color: white;
    overflow: auto;
  }

  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.7);
    z-index: 3;
  }
@media only screen and (max-width: 700px) {

    .Overlay{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.7);
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Modal{
        position: absolute;
        left: 0;
        top: auto;
        bottom: auto;
        right: 0;
        z-index: 3;
        background-color: white;
    }
    .mas-container {
        grid-column: 3/11;
    }
}
